    /**
     * 
     * @param {*} loginId
     * @param {*} loginPwd
     */
    async userLogin(loginId, loginPwd) {
        return await axios().post('/api/user/login', {
            loginId,
            loginPwd
        }, {
            headers: {
                type: 'user'
            }
        })
    },
