import Vue from 'vue'
import ElementUI from 'element-ui'
import App from './App.vue'
import router from './router'
import store from './store'
import api from './api/api'
import 'element-ui/lib/theme-chalk/index.css'
import './assets/scss/util/reset.css'
import './assets/scss/util/util.scss'

api.whoAmI().then(res => {
    if (res.data !== null) {
        console.log("");
        store.dispatch('changeUser', res)
    }
}).catch(err => {
    console.log("");
})

api.adminWhoAmI().then(res => {
    if (res) {
        console.log("");

        store.dispatch('changeAdmin', res);
    }
}).catch(err => {
    console.log("")
});

api.cinemaWhoAmI().then(res => {
    if (res) {
        console.log("");
        store.dispatch('changeCinema', res);
    }
}).catch(err => {
    console.log("")
});


Vue.prototype.$api = api

Vue.config.productionTip = false
Vue.use(ElementUI)

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')